import *  as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'

export function startBasicScene() {
    // SCENE
    const scene = new THREE.Scene();
    // CAMERA
    const camera = new THREE.PerspectiveCamera(75, 2, 0.1, 5);
    camera.position.z = 2;
    // RENDERER
    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(window.innerWidth, window.innerHeight, false);

    // CANVAS, CONTEXT => TEXTURE => MESHMATERIAL 
    const c = document.createElement('canvas')
    const ctx = c.getContext('2d');
    ctx.canvas.width = window.innerWidth;
    ctx.canvas.height = window.innerHeight;
    ctx.fillStyle = '#FFF';
    ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    const texture = new THREE.CanvasTexture(ctx.canvas);
    const material = new THREE.MeshBasicMaterial({
        map: texture,
    });

    // CREATE PLANE, ADD TO SCENE
    const planeWidth = 5
    const planeHeight = 3
    const geometry = new THREE.PlaneGeometry(planeWidth, planeHeight)
    const plane = new THREE.Mesh(geometry, material);
    // window.innerHeight / window.innerWidth
    plane.rotation.y = window.innerHeight / (window.innerWidth - 0.1)// 0.5
    plane.receiveShadow = true
    scene.add(plane);

    // CREATE POINT LIGHTS, ADD TO SCENE
    // INIT HEMISPHERE LIGHT
    scene.add(new THREE.AmbientLight(0xffffff, 0.5));
    
    const light1 = new THREE.PointLight(0xff6666, 1, 100);
    light1.castShadow = true;
    light1.shadow.mapSize.width = 4096;
    light1.shadow.mapSize.height = 4096;
    scene.add(light1);

    const light2 = new THREE.PointLight(0x33ff33, 1, 100);
    light2.castShadow = true;
    light2.shadow.mapSize.width = 4096;
    light2.shadow.mapSize.height = 4096;
    scene.add(light2);

    // ESTABLISH MATRIX RAIN FONT DATA
    //chinese characters - taken from the unicode charset
    var chineseChars = "田由甲申甴电甶男甸甹町画甼甽甾甿畀畁畂畃畄畅畆畇畈畉畊畋界畍畎畏畐畑";
    //converting the string into an array of single characters
    var chinese = chineseChars.split("");

    var font_size = 10;
    var columns = c.width/font_size; //number of columns for the rain
    //an array of drops - one per column
    var drops = [];
    //x below is the x coordinate
    //1 = y co-ordinate of the drop(same for every drop initially)
    for(var x = 0; x < columns; x++)
        drops[x] = 1; 


    // DRAW FUNCTION 
    function drawFunction() {
        console.log(chinese)
        console.log(drops)
        //Black BG for the canvas
        //translucent BG to show trail
        ctx.fillStyle = "rgba(0, 0, 0, 0.05)";
        ctx.fillRect(0, 0, c.width, c.height);
        console.log(c.width, c.height)
        ctx.fillStyle = "#0F0"; //green text
        ctx.font = font_size + "px arial";
        //looping over drops
        for(var i = 0; i < drops.length; i++)
        {
            //a random chinese character to print
            var text = chinese[Math.floor(Math.random()*chinese.length)];
            //x = i*font_size, y = value of drops[i]*font_size
            ctx.fillText(text, i*font_size, drops[i]*font_size);
            
            //sending the drop back to the top randomly after it has crossed the screen
            //adding a randomness to the reset to make the drops scattered on the Y axis
            if(drops[i]*font_size > c.height && Math.random() > 0.975)
                drops[i] = 0;
            
            //incrementing Y coordinate
            drops[i]++;
        }

    }

    // ORBIT CONTROLS
    // const controls = new OrbitControls(camera, renderer.domElement);
    // controls.target = new THREE.Vector3(0, 0, -40);
    // controls.update();

    // RESIZE HANDLER
    function resizeRendererToDisplaySize(renderer) {
        const canvas = renderer.domElement;
        const width = canvas.clientWidth;
        const height = canvas.clientHeight;
        const needResize = canvas.width !== width || canvas.height !== height;
        if (needResize) {
            renderer.setSize(width, height, false);
        }
        return needResize;
    }
    
    // ANIMATE/RENDER FUNCTION, CALLED RECURSIVELY
    function render() {
        if (resizeRendererToDisplaySize(renderer)) {
            const canvas = renderer.domElement;
            camera.aspect = canvas.clientWidth / canvas.clientHeight;
            camera.updateProjectionMatrix();
          }
        drawFunction();
        texture.needsUpdate = true;
        renderer.render(scene, camera);
        requestAnimationFrame(render);
    }
    // window resize handler
    function onWindowResize() {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(window.innerWidth, window.innerHeight);
    }
    window.addEventListener('resize', onWindowResize);
    const renderElement = renderer.domElement
    renderElement.classList.add("background-animation")
    document.body.appendChild(renderElement);  // append Three.js canvas to DOM 
    render(); // initial call to recursive render function
}